.contact {  
    height: calc(100vh); 
    display: flex; 
    flex-direction: column; 
    align-items: center; 
    justify-content: center;
    .contact-main { 
      display: flex;
      justify-content: center;
      align-items: center; 
      padding: 0 80px;  
      .contact-infor { 
        width: 50%;  
        h2 { 
          font-size: 32px;  
          margin-top: 20px;
          margin-bottom: 32px;
        } 
        p { 
          margin-bottom: 32px;
          font-size: 20px;
        } 
        .contact-mail { 
          font-weight: 600; 
          font-size: 18px;
        }
      }
    
      .contact-form {
        margin: 40px auto 0;
        max-width: 400px;
        width: 50%;
    
        label {
          display: block;
          margin-bottom: 10px;
        }
    
        input,
        textarea {
          display: block;
          width: 100%;
          padding: 16px;
          border: 1px solid #ccc; 
          font-weight: 600;
          border-radius: 5px;
          margin-bottom: 20px;
        }
    
        button[type="submit"] {
          display: inline-block;
          padding: 12px 40px;
          font-size: 16px;
          font-weight: 600;
          color: #fff;
          outline: none;
          border: none;
          border-radius: 12px;
          background-color: var(--primary);
          cursor: pointer; 
          float: right;
        }
      }
    }

  }