.about {
    height: 100vh;
    margin-top: var(--height-header);
    display: flex;
    flex-direction: column;
    justify-content: center; 

    .about-wrapper {
        display: flex;
        padding: 0 80px;
        justify-content: center;
        align-items: center;
        margin-top: 40px;

        img {
            width: 40%;
            height: 400px;
            border-radius: 20px;
            margin-right: 40px;
        }

        .about-infor {
            width: 60%;

            h2 {
                text-align: left;
                font-size: 24px;
            }

            p {
                text-align: left;
                font-size: 20px;
                margin-bottom: 12px;
            } 
            .heading-sub { 
              font-size: 20px;
            }
        }
    }
    .list {
        display: flex; 
        flex-wrap: wrap;
        li {
            display: flex;
            align-items: center;
            justify-content: center;
            font-weight: 600;
            font-size: 20px;
            list-style: none; 
            margin-top: 12px;
            margin-right: 20px;


            img {
                width: 40px;
                height: 40px;
                margin-right: 4px;
            }
        }
    }
}
